import { BigNumber } from 'ethers';
import React, { useEffect, useState } from 'react';
import { Form, Card } from 'react-bootstrap';
import useBlockchain from '../../hooks/useBlockchain';
import useLocalStorage from '../../hooks/useLocalStorage';

const ChangeWallet = () => {
    const { getAccount } = useLocalStorage('');
    const [contract, , , active] = useBlockchain('');
    const [walletAddress, setWalletAddress] = useState('');
    const [newWalletAddress, setNewWalletAddress] = useState('')

    const accountID = getAccount();

    useEffect(() => {
        if (!active) return;
        const fn = async () => {
            const user = await contract.getUser(BigNumber.from(accountID));
            setWalletAddress(user.addr);
        };
        fn();
    }, [contract, getAccount, active, accountID]);

    const changeWallet = async () => {
        await contract.changeWallet(getAccount(), newWalletAddress)
        window.alert('Submitted')
    }


    return (
        <>
            <div className="row">
                <div className="col-md-6 offset-md-3">
                    <Card>
                        <Card.Header>
                            <Card.Title as="h5">Change Wallet Address <span className='leftS'>ID: {accountID}</span></Card.Title>
                        </Card.Header>
                        <Card.Body>
                            <Form>
                                <Form.Group>
                                    <Form.Label>Current Address</Form.Label>
                                    <Form.Control type="text" value={walletAddress} id="#" readOnly />
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>New Wallet Address </Form.Label>
                                    <Form.Control value={newWalletAddress} onChange={e=>setNewWalletAddress(e.target.value)} type="text" id="walletAddress" placeholder="Enter new Address" />
                                </Form.Group>

                                <input onClick={changeWallet} type="button" className="btn btn-primary btn-block mb-4" value="Save" />
                            </Form>
                        </Card.Body>
                    </Card>
                </div>
            </div>
        </>
    );
};

export default ChangeWallet;
